
.section-custom-content {
	.section-container {
		>.flex {
			align-items: center;
		}
	}
	.block-paragraph {
		p {
			margin: 0;
		}
		.text-1 {
			margin-bottom: 25px;
			line-height: 1.2em;
			font-weight: 500;
		}
		.text-2 {
			color: var(--subtext-color);
			margin-bottom: 25px;
			line-height: 1.78em;
		}
	}
	@media (min-width: 750px) {
		.block-paragraph {
			.text-1 {
				margin-bottom: 30px;
			}
			.text-2 {
				margin-bottom: 30px;
			}
		}
	}
}

.page-about {
	.section-custom-content {
		.block-item {
			margin: 0 auto;
		}
		.block-paragraph {
			.text-1 {
				p {
					line-height: 1.2em;
					font-weight: inherit;
				}
			}
			.text-2 {
				p {
					line-height: 1.45em;
					font-weight: 400;
				}
			}
		}
	}
}